import fullScreen from './fullscreen';


const stop = false;
let fps = 2;
let now;
let then = Date.now();
let elapsed;
let arrayFrames = document.querySelectorAll('.frame--item_canvas');
let count = 0;


function drawAnimate() {
  const index = count % arrayFrames.length;
  count += 1;
  const frame = arrayFrames[index];
  const { width, height } = frame;

  const canvasAnimate = document.getElementById('animate_canvas');
  const ctxAnim = canvasAnimate.getContext('2d');
  canvasAnimate.width = width;
  canvasAnimate.height = height;

  ctxAnim.drawImage(frame, 0, 0, width, height, 0, 0, width, height);
}

function animateLoop() {
  if (stop) return;
  requestAnimationFrame(animateLoop);

  now = Date.now();
  elapsed = now - then;
  if (elapsed > 1000 / fps) {
    then = now - (elapsed % (1000 / fps));
    drawAnimate();
  }
}

function changeFPS() {
  const { value } = document.getElementById('fps_range');
  document.getElementById('fps_text').innerText = `${value} FPS`;
  fps = value;
}

function getArrayFrames() {
  arrayFrames = document.querySelectorAll('.frame--item_canvas');
}

export default function initAnimate() {
  document.addEventListener('click', getArrayFrames);
  document.getElementById('fps_range').addEventListener('input', changeFPS);
  animateLoop();

  document.getElementById('full_screen').addEventListener('click', fullScreen);
}
