import removeClassSelected from '../utils/removeClassSelected';
import checkKeyPress from './hotKeys';
import { initToolsAfterReload, initCanvasAfterReload } from './localStorage';

import pencil from '../../tools/pencil';
import stroke from '../../tools/stroke';
import bucket from '../../tools/bucket';
import bucketAllPixels from '../../tools/bucketAllPixels';
import picker from '../../tools/picker';
import eraser from '../../tools/eraser';

export default class Main {
  constructor() {
    this.canvas = document.getElementById('canvas');
    this.canvas2 = document.getElementById('canvas2');
    this.ctx = this.canvas.getContext('2d');
    this.pixelSize = localStorage.getItem('pixelSize') || 1;
    this.canvasSize = localStorage.getItem('canvasSize') || 32;
    this.canvas.width = this.canvasSize;
    this.canvas.height = this.canvasSize;
    this.firstColor = localStorage.getItem('firstColor') || '#10ec10';
    this.secondColor = localStorage.getItem('secondColor') || '#db11c0';
    this.x = 0;
    this.y = 0;
    this.toolChoose = localStorage.getItem('toolChoose') || 'pencil';
    this.tools = {
      pencil: (event) => pencil.call(this, event),
      stroke: (event) => stroke.call(this, event),
      bucket: (event) => bucket.call(this, event),
      bucketAllPixels: (event) => bucketAllPixels.call(this, event),
      picker: (event) => picker.call(this, event),
      eraser: (event) => eraser.call(this, event),
    };
  }

  drawFirstImage() {
    this.ctx.strokeStyle = 'rgba(219, 17, 192, 255)';
    this.ctx.lineWidth = 2;
    this.ctx.strokeRect(2, 2, 10, 10);
    this.ctx.strokeRect(5, 5, 20, 20);
  }


  setColor(event) {
    window.oninput = () => {
      const elemPalette = event.target;
      const colorCelected = event.target.firstElementChild.value;

      elemPalette.style.backgroundColor = colorCelected;
      if (event.target.id === 'first_color') { this.firstColor = colorCelected; }
      if (event.target.id === 'second_color') { this.secondColor = colorCelected; }
    };
  }

  setPenSize(event) {
    if (!(event.target.id === 'pen_sizes')) {
      removeClassSelected('.pen_size--item');

      event.target.classList.add('selected');
      this.pixelSize = event.target.id.slice(-1);
    }
  }

  setTools(event) {
    if (!(event.target.id === 'tools')) {
      removeClassSelected('.tools--item');

      event.target.classList.add('selected');
      this.toolChoose = event.target.id;
    }
  }

  setCanvasSize(event) {
    if (!(event.target.id === 'canvas_size')) {
      removeClassSelected('.canvas_size--item');
      event.target.classList.add('selected');
      const [newCanvasSize] = event.target.innerText.split('x');

      const { width, height } = this.canvas;
      const imageFromCanvas = this.ctx.getImageData(0, 0, width, height);
      this.canvas.width = newCanvasSize;
      this.canvas.height = newCanvasSize;
      this.canvasSize = newCanvasSize;
      this.ctx.putImageData(imageFromCanvas, 0, 0);

      const arrayFrames = document.querySelectorAll('.frame--item');
      arrayFrames.forEach((item) => {
        const frame = item.firstElementChild;
        const ctxFrame = frame.getContext('2d');
        const imageFromFrame = ctxFrame.getImageData(0, 0, width, height);
        frame.width = newCanvasSize;
        frame.height = newCanvasSize;
        ctxFrame.putImageData(imageFromFrame, 0, 0);
      });

      const canvas2 = document.getElementById('canvas2');
      canvas2.width = newCanvasSize;
      canvas2.height = newCanvasSize;
    }
  }

  init() {
    initToolsAfterReload.call(this);
    initCanvasAfterReload.call(this);

    /* tools listener */
    document.getElementById('palette').addEventListener('mousedown', (event) => this.setColor(event));
    document.getElementById('pen_sizes').addEventListener('mousedown', (event) => this.setPenSize(event));
    document.getElementById('tools').addEventListener('mousedown', (event) => this.setTools(event));
    document.getElementById('canvas_size').addEventListener('mousedown', (event) => this.setCanvasSize(event));

    /* canvas listener */
    this.canvas2.addEventListener('mousemove', (event) => {
      if (this.toolChoose === 'pencil'
       || this.toolChoose === 'eraser'
       || this.toolChoose === 'stroke'
      ) this.tools[this.toolChoose](event);
    });

    this.canvas2.addEventListener('mousedown', (event) => {
      this.isDrawing = true;
      this.tools[this.toolChoose](event);
    });

    document.addEventListener('mouseup', () => { this.isDrawing = false; });
    document.addEventListener('keydown', checkKeyPress.bind(this));
  }
}
