import { main } from './init';
import removeClassSelected from '../utils/removeClassSelected';

function createSprite() {
  main.ctx.clearRect(0, 0, main.canvasSize, main.canvasSize);

  removeClassSelected('.selected');

  const arrayFrames = document.querySelectorAll('.frame--item');
  arrayFrames.forEach((item, i) => {
    const frame = item;
    if (i === 0) {
      frame.classList.add('selected');
      const frameCanvas = frame.firstElementChild;
      const ctxFrame = frameCanvas.getContext('2d');
      ctxFrame.clearRect(0, 0, main.canvasSize, main.canvasSize);
    }
    if (i > 0) frame.remove();
  });

  main.canvas.width = 32;
  main.canvas.height = 32;
  main.firstColor = '#10ec10';
  main.secondColor = '#db11c0';
  main.canvasSize = 32;
  main.toolChoose = 'pencil';
  main.pixelSize = 1;

  const canvasTop = document.getElementById('canvas2');
  canvasTop.width = 32;
  canvasTop.height = 32;

  document.getElementById('first_color').style.backgroundColor = main.firstColor;
  document.getElementById('first_color').firstElementChild.value = main.firstColor;

  document.getElementById('second_color').style.backgroundColor = main.secondColor;
  document.getElementById('second_color').firstElementChild.value = main.secondColor;

  document.getElementById(`pen_size${main.pixelSize}`).classList.add('selected');
  document.getElementById(main.toolChoose).classList.add('selected');
  document.getElementById(`canvas_size${main.canvasSize}`).classList.add('selected');
}

export default function initcreateSprite() {
  document.getElementById('createSprite').addEventListener('mousedown', createSprite);
}
