export default function getCoorPointCanvas(event, canvasSize, pixelSize) {
  const pixelSizeArr = [0, 1, 1, 2];
  const size = pixelSizeArr[pixelSize - 1];
  const scale = 512 / canvasSize;
  return {
    x1: Math.trunc((event.offsetX - size) / scale),
    y1: Math.trunc((event.offsetY - size) / scale),
  };
}
