
import {
  setFrame, setFrameActive, setCurrentOrderFrames, deleteFrame, drawFrameOnCanvas, copyFrame,
} from './utils';
import trackFrame from './dragAndDrop';
import addFrame from './addFrame';
import initFramesAfterReload from './localStorage';


let orderFrame;

const mouseDownHandler = (event) => {
  if (event.target.classList.contains('btn_delete')) {
    if (orderFrame === 1) return;
    const frameDelete = event.target.closest('LI');
    const orderFrameDelete = frameDelete.style.order;
    orderFrame -= 1;

    deleteFrame(event);
    setCurrentOrderFrames();

    if (frameDelete.classList.contains('selected')) {
      setFrameActive(event, orderFrameDelete);
    }

    drawFrameOnCanvas();
  } else if (event.target.classList.contains('frame--item_canvas')) {
    setFrameActive(event);
    drawFrameOnCanvas();
  } else if (event.target.classList.contains('btn_copy')) {
    copyFrame(event);
    orderFrame += 1;
    setCurrentOrderFrames();
  }
};


export default function initframe() {
  orderFrame = +localStorage.getItem('numberFrames') || 1;
  initFramesAfterReload();

  document.getElementById('canvas2').addEventListener('mouseup', () => setFrame());
  document.getElementById('frames').addEventListener('mousedown', mouseDownHandler);
  document.getElementById('btn_add_frame').addEventListener('mousedown', () => {
    orderFrame += 1;
    addFrame(orderFrame);
    drawFrameOnCanvas();
  });

  trackFrame();
}
