import getCoorPointCanvas from '../screens/utils/coorPointCanvas';

export default function picker(event) {
  const { x1, y1 } = getCoorPointCanvas(event, this.canvasSize, this.pixelSize);
  const { data } = this.ctx.getImageData(x1, y1, 1, 1);
  let [r, g, b, a] = data;

  r = r.toString(16);
  g = g.toString(16);
  b = b.toString(16);
  a = Math.round(a).toString(16);

  function getFullLength(hex) {
    return hex.length === 1 ? `0${hex}` : hex;
  }

  const newColor = `#${getFullLength(r)}${getFullLength(g)}${getFullLength(b)}${getFullLength(a)}`;

  if (event.buttons === 1) {
    this.firstColor = newColor;
    document.getElementById('first_color').style.backgroundColor = newColor;
    document.getElementById('first_color_input').value = newColor;
  }
  if (event.buttons === 2) {
    this.secondColor = newColor;
    document.getElementById('second_color').style.backgroundColor = newColor;
    document.getElementById('second_color_input').value = newColor;
  }
}
