import removeClassSelected from '../utils/removeClassSelected';

export default function addFrame(orderFrame) {
  removeClassSelected('.frame--item');

  const frame = `
  <li class="frame--item selected" draggable="true">
    <canvas class="frame--item_canvas"  width="32" height="32">
    </canvas>
    <div class="frame--btn_number">${orderFrame}</div>
    <div class="frame--btn btn_delete"></div>
    <div class="frame--btn btn_copy"></div>
    <div class="frame--btn btn_move"></div>
  </li>
  `;
  document.getElementById('frames').insertAdjacentHTML('beforeend', frame);
  document.getElementById('frames').lastElementChild.style.order = orderFrame;
}
