import removeClassSelected from '../utils/removeClassSelected';

let that;

function setToolsKeyboard(element) {
  removeClassSelected('.tools--item');
  const el = document.getElementById(element);
  el.classList.add('selected');
  that.toolChoose = element;
}

export default function checkKeyPress(e) {
  that = this;
  switch (e.code) {
    case 'KeyP':
      setToolsKeyboard('pencil');
      break;
    case 'KeyL':
      setToolsKeyboard('stroke');
      break;
    case 'KeyB':
      setToolsKeyboard('bucket');
      break;
    case 'KeyA':
      setToolsKeyboard('bucketAllPixels');
      break;
    case 'KeyO':
      setToolsKeyboard('picker');
      break;
    case 'KeyE':
      setToolsKeyboard('eraser');
      break;
    default:
  }
}
