import getCoorPointCanvas from '../screens/utils/coorPointCanvas';

export default function bucket(event) {
  let newColor;
  let oldColor = 0;
  let colorPoint;
  let pixel;
  let point;
  const imageData = this.ctx.getImageData(0, 0, this.canvasSize, this.canvasSize);
  const { x1, y1 } = getCoorPointCanvas(event, this.canvasSize, this.pixelSize);
  const { data } = this.ctx.getImageData(x1, y1, 1, 1);
  const stack = [[x1, y1]];

  oldColor = data.reduce((acc, item) => acc + item);

  if (event.buttons === 1) newColor = this.firstColor;
  if (event.buttons === 2) newColor = this.secondColor;

  const newColorArray = newColor.match(/[A-Za-z0-9]{2}/g).map((v) => parseInt(v, 16));
  if (newColorArray.every((item, ind) => item === data[ind])) return;

  while (stack.length > 0) {
    pixel = stack.pop();

    if (pixel.every((i) => i > 0) || pixel.every((i) => i <= this.canvasSize)) {
      point = (pixel[1] * this.canvasSize + pixel[0]) * 4;
      colorPoint = 0;

      for (let i = 0; i < 4; i += 1) {
        colorPoint += imageData.data[point + i];
      }

      if (colorPoint === oldColor) {
        // paint pixel
        for (let i = 0; i < 3; i += 1) {
          imageData.data[point + i] = newColorArray[i];
        }
        imageData.data[point + 3] = 255;

        // add adjacent pixels to the stack
        stack.push([
          pixel[0] - 1,
          pixel[1],
        ]);
        stack.push([
          pixel[0] + 1,
          pixel[1],
        ]);
        stack.push([
          pixel[0],
          pixel[1] - 1,
        ]);
        stack.push([
          pixel[0],
          pixel[1] + 1,
        ]);
      }
    }
  }
  this.ctx.putImageData(imageData, 0, 0);
}
