import getCoorPointCanvas from '../screens/utils/coorPointCanvas';

export default function stroke(event) {
  if (!this.isDrawing) {
    this.x = 0;
    this.y = 0;
    this.snapshotStart = 0;
    return;
  }

  if (event.buttons === 1) this.ctx.fillStyle = this.firstColor;
  if (event.buttons === 2) this.ctx.fillStyle = this.secondColor;

  this.ctx.lineCap = 'round';

  const coorEndPoint = getCoorPointCanvas(event, this.canvasSize, this.pixelSize);
  const { x1, y1 } = coorEndPoint;

  this.x = this.x || x1;
  this.y = this.y || y1;
  let x0 = this.x;
  let y0 = this.y;

  // draw first point
  this.ctx.fillRect(x0, y0, this.pixelSize, this.pixelSize);

  if (!this.snapshotStart) {
    this.snapshotStart = this.ctx.getImageData(0, 0, this.canvasSize, this.canvasSize);
  }

  this.ctx.putImageData(this.snapshotStart, 0, 0);

  // bresenham's line algorithm
  const dx = Math.abs(x1 - x0);
  const dy = Math.abs(y1 - y0);
  const sx = (x0 < x1) ? 1 : -1;
  const sy = (y0 < y1) ? 1 : -1;
  let err = dx - dy;

  while (!((x0 === x1) && (y0 === y1))) {
    this.ctx.fillRect(x0, y0, this.pixelSize, this.pixelSize);

    const e2 = 2 * err;
    if (e2 > -dy) { err -= dy; x0 += sx; }
    if (e2 < dx) { err += dx; y0 += sy; }

    this.ctx.fillRect(x0, y0, this.pixelSize, this.pixelSize);
  }
}
