
function initToolsAfterReload() {
  document.getElementById('first_color').style.backgroundColor = this.firstColor;
  document.getElementById('first_color').firstElementChild.value = this.firstColor;

  document.getElementById('second_color').style.backgroundColor = this.secondColor;
  document.getElementById('second_color').firstElementChild.value = this.secondColor;

  document.getElementById(`pen_size${this.pixelSize}`).classList.add('selected');
  document.getElementById(this.toolChoose).classList.add('selected');
  document.getElementById(`canvas_size${this.canvasSize}`).classList.add('selected');
}

function initCanvasAfterReload() {
  if (localStorage.getItem('imageDataURL')) {
    const dataURL = localStorage.getItem('imageDataURL');
    const img = new Image();
    img.src = dataURL;
    img.onload = () => {
      this.ctx.drawImage(img, 0, 0);
    };
  } else {
    this.drawFirstImage();
  }
}


export { initToolsAfterReload, initCanvasAfterReload };
