import { main } from './init';
import getCoorPointCanvas from '../utils/coorPointCanvas';


const canvasTop = document.getElementById('canvas2');
const ctx = canvasTop.getContext('2d');
canvasTop.width = localStorage.getItem('canvasSize') || 32;
canvasTop.height = localStorage.getItem('canvasSize') || 32;
const arrTools = ['pencil', 'stroke', 'eraser'];

function getPixelSize() {
  const isDrawingTool = arrTools.some((tool) => tool === main.toolChoose);
  const pixelSize = (isDrawingTool) ? +main.pixelSize : 1;
  return pixelSize;
}

function drawCursorPoint(event) {
  const pixelSize = getPixelSize();
  const coorPoint = getCoorPointCanvas(event, main.canvasSize, pixelSize);
  const { x1, y1 } = coorPoint;

  ctx.clearRect(0, 0, main.canvasSize, main.canvasSize);
  ctx.fillStyle = 'rgba(255, 255, 255, 0.3)';
  ctx.fillRect(x1, y1, pixelSize, pixelSize);
}

function initCursorPoint() {
  canvasTop.addEventListener('mousemove', drawCursorPoint);
}

export default initCursorPoint;
