import removeClassSelected from '../utils/removeClassSelected';

function setFrame() {
  const imageCanvas = document.getElementById('canvas');
  const { width, height } = imageCanvas;

  const activeFrame = document.querySelector('.frame--item.selected').firstElementChild;
  const ctxFrame = activeFrame.getContext('2d');

  activeFrame.width = width;
  activeFrame.height = height;
  ctxFrame.drawImage(imageCanvas, 0, 0, width, height, 0, 0, width, height);
}


function drawFrameOnCanvas() {
  const imageFrame = document.querySelector('.frame--item.selected').firstElementChild;
  const { width, height } = imageFrame;

  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');

  ctx.clearRect(0, 0, canvas.width, canvas.height);
  ctx.drawImage(imageFrame, 0, 0, width, height, 0, 0, width, height);
}


function setFrameActive(event, order = 0) {
  removeClassSelected('.frame--item');

  const arrFrames = document.querySelectorAll('.frame--item');

  if (!order) {
    event.target.closest('LI').classList.add('selected');
    return;
  }
  removeClassSelected('.frame--item');

  if (order - 1 === arrFrames.length) {
    arrFrames[order - 2].classList.add('selected'); return;
  }

  if (order === 1) arrFrames[order].classList.add('selected');
  if (order) arrFrames[order - 1].classList.add('selected');
}


function setCurrentOrderFrames() {
  const arrayFrames = document.querySelectorAll('.frame--item');
  const arrayNumberFrames = document.querySelectorAll('.frame--btn_number');

  arrayFrames.forEach((item, ind) => {
    const item1 = item;
    item1.style.order = ind + 1;
  });

  arrayNumberFrames.forEach((item, ind) => {
    const item1 = item;
    item1.innerText = ind + 1;
  });
}


function deleteFrame(event) {
  const element = event.target.closest('LI');
  element.remove();
}

function copyFrame(event) {
  const element = event.target.closest('LI').outerHTML;
  const { order } = event.target.closest('LI').style;
  const frameCanvasOld = event.target.closest('LI').firstElementChild;
  const { width, height } = frameCanvasOld;

  event.target.closest('LI').insertAdjacentHTML('afterend', element);
  setFrameActive(event, +order + 1);

  const frameCanvasNew = document.querySelector('.frame--item.selected').firstElementChild;
  const ctxNew = frameCanvasNew.getContext('2d');

  ctxNew.drawImage(frameCanvasOld, 0, 0, width, height, 0, 0, width, height);
}


export {
  setFrame, setFrameActive, setCurrentOrderFrames, deleteFrame, drawFrameOnCanvas, copyFrame,
};
