import { main } from '../canvas/init';


function setLocalStorage() {
  const { canvasSize } = main;
  const imageDataURL = main.canvas.toDataURL();

  localStorage.setItem('canvasSize', canvasSize);
  localStorage.setItem('imageDataURL', imageDataURL);

  const arrayFrames = document.querySelectorAll('.frame--item');
  arrayFrames.forEach((item, i) => {
    const frame = item.firstElementChild;
    const dataURLFrame = frame.toDataURL();
    localStorage.setItem(`dataURLFrame${i}`, dataURLFrame);
  });

  localStorage.setItem('pixelSize', main.pixelSize);
  localStorage.setItem('toolChoose', main.toolChoose);
  localStorage.setItem('firstColor', main.firstColor);
  localStorage.setItem('secondColor', main.secondColor);
  localStorage.setItem('numberFrames', arrayFrames.length);
}


export default function initLocalStorage() {
  window.addEventListener('beforeunload', setLocalStorage);
}
