import GIF from 'gif.js.optimized';
import download from 'downloadjs';

export default function exportGIF() {
  const arrayFrames = document.querySelectorAll('.frame--item_canvas');
  const { width, height } = arrayFrames;
  const { value } = document.getElementById('fps_range');
  const gif = new GIF({
    workers: 2,
    quality: 10,
    workerScript: './gif.worker.js',
    repeat: 0,
    width,
    height,
    background: arrayFrames[0].style.background,
  });

  arrayFrames.forEach((frame) => gif.addFrame(frame, { delay: 1000 / value }));

  gif.on('finished', (blob) => {
    download(blob, 'piskel_image.gif', 'gif');
  });

  gif.render();
}
