import { setCurrentOrderFrames } from './utils';

let dragElement = 0;

function dragStartHandler(event) {
  const { target } = event;
  target.style.opacity = '1';

  dragElement = target.closest('.frame--item');

  const e = event;
  e.dataTransfer.effectAllowed = 'move';
  e.dataTransfer.setData('text/html', target);
}

function dragOverHandle(event) {
  if (event.preventDefault) {
    event.preventDefault();
  }

  const e = event;
  e.dataTransfer.dropEffect = 'move';

  return false;
}

function dragEnterHandle(event) {
  const { target } = event;

  if (target.tagName.toLowerCase() === 'canvas') {
    target.closest('.frame--item').classList.add('over');
  }
}

function dragLeaveHandle(event) {
  const { target } = event;

  if (target.tagName.toLowerCase() === 'canvas') {
    target.closest('.frame--item').classList.remove('over');
  }
}

function dropHandle(event) {
  const underElement = event.target.closest('.frame--item');

  if (underElement.style.order < dragElement.style.order) {
    underElement.before(dragElement);
  }
  if (underElement.style.order > dragElement.style.order) {
    underElement.after(dragElement);
  }
}

function dragEndHandle(event) {
  const over = document.querySelector('.over');
  if (over) {
    over.classList.remove('over');
  }

  const { target } = event;
  target.style.opacity = '';

  setCurrentOrderFrames();
}

export default function trackFrame() {
  const frameList = document.getElementById('frames');

  frameList.addEventListener('dragstart', dragStartHandler);
  frameList.addEventListener('dragenter', dragEnterHandle);
  frameList.addEventListener('dragover', dragOverHandle);
  frameList.addEventListener('dragleave', dragLeaveHandle);
  frameList.addEventListener('drop', dropHandle);
  frameList.addEventListener('dragend', dragEndHandle);
}
