import getCoorPointCanvas from '../screens/utils/coorPointCanvas';

export default function bucketAllPixels(event) {
  let newColor;

  const canvasData = this.ctx.getImageData(0, 0, this.canvasSize, this.canvasSize);
  const { x1, y1 } = getCoorPointCanvas(event, this.canvasSize, this.pixelSize);
  const pointData = this.ctx.getImageData(x1, y1, 1, 1);

  if (event.buttons === 1) newColor = this.firstColor;
  if (event.buttons === 2) newColor = this.secondColor;

  const newColorArray = newColor.match(/[A-Za-z0-9]{2}/g).map((v) => parseInt(v, 16));
  newColorArray.push(255);

  for (let i = 0; i < canvasData.data.length; i += 4) {
    if (pointData.data.every((item, ind) => item === canvasData.data[i + ind])) {
      for (let j = 0; j < 4; j += 1) {
        canvasData.data[i + j] = newColorArray[j];
      }
    }
  }
  this.ctx.putImageData(canvasData, 0, 0);
}
