import UPNG from 'upng-js';
import download from 'downloadjs';


export default function exportAPNG() {
  const arrayFrames = document.querySelectorAll('.frame--item_canvas');
  const { width, height } = arrayFrames[0];
  const { value } = document.getElementById('fps_range');
  const buf = [];

  for (let i = 0; i < arrayFrames.length; i += 1) {
    const ctx = arrayFrames[i].getContext('2d');
    const image = ctx.getImageData(0, 0, arrayFrames[0].width, arrayFrames[0].height);
    const buf1 = image.data.buffer;
    buf.push(buf1);
  }

  const delays = new Array(buf.length).fill(1000 / value);

  const data = UPNG.encode(buf, width, height, 0, delays);

  download(data, 'piskel_image.apng', 'apng');
}
