import addFrame from './addFrame';
import { main } from '../canvas/init';


export default function initFramesAfterReload() {
  const numberFrames = localStorage.getItem('numberFrames');

  for (let i = 1; i < numberFrames; i += 1) addFrame(i + 1);

  const arrayFrames = document.querySelectorAll('.frame--item');

  arrayFrames.forEach((item, i) => {
    const frame = item.firstElementChild;
    const ctxFrame = frame.getContext('2d');
    frame.width = main.canvasSize;
    frame.height = main.canvasSize;
    const dataURLFrame = localStorage.getItem(`dataURLFrame${i}`);
    const img = new Image();
    img.src = dataURLFrame;
    img.onload = () => {
      ctxFrame.drawImage(img, 0, 0);
    };
  });
}
